:root {
  --black: #111;
  --orange: #fd9222;
  --blackish: #1e1823;
  --beige: #fafafa;
  --pureblack: #000;
  --grey: rgba(17, 17, 17, 0.7);
  --light-orange: #fcf1e0;
}

body {
  font-family: Montserrat;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  letter-spacing: -0.28px;
}

h1 {
  font-family: Montserrat;
  font-size: 28px;
  font-style: normal;
  font-weight: 500;
  line-height: 1.21;
  letter-spacing: -0.84px;
}

h2 {
  font-family: Montserrat;
  font-size: 24px;
  font-style: normal;
  font-weight: 600;
  line-height: 1.16;
  letter-spacing: -0.72px;
  text-transform: uppercase;
}

h3 {
  font-family: Montserrat;
  font-size: 20px;
  font-style: normal;
  font-weight: 600;
  line-height: 24px;
  letter-spacing: -0.6px;
  text-transform: uppercase;
}

h4 {
  font-family: Montserrat;
  font-size: 18px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
}

.list {
  list-style: none;
}
